        <ac-view class="index">
            <ac-view class="index-hd">
                <ac-image class="index-logo" src="./static/icon_API.png"></ac-image>
                <ac-view class="index-desc">ACUIACUI</ac-view>
            </ac-view>
            <ac-view class="index-bd">
                <ac-view class="kind-list">
                    <ac-block ac:for="(item, id) in list" :key="id">
