          name: '',
          open: false,
          // pages: ['view', 'scroll-view', 'swiper']
          pages: [
            {
              path: '/pages/ACAPIrequset',
              title: ''
            },
            {
              path: '/pages/ACAPIuploadFile',
              title: ''
            },
            {
              path: '/pages/ACAPIdownloadFile',
              title: ''
            },
            {
              path: '/pages/ACAPIstorage',
              title: ''
            },
            {
              path: '/pages/ACAPIsqlite',
              title: ''
            }
          ]
        }, {
          id: 'media',
          name: '',
          open: false,
          pages: [
