              title: ''
            },
            {
              path: '/pages/ACAPIchooseLocation',
              title: ''
            },
            {
              path: '/pages/ACAPIopenLocation',
              title: ''
            }
          ]

        }, {
          id: 'system',
          name: '',
          open: false,
          pages: [
            {
              path: '/pages/ACAPIsystemInfo',
              title: ''
            },
            {
              path: '/pages/ACAPInetworkStatus',
              title: ''
            },
            {
              path: '/pages/ACAPIaccelerometer',
              title: ''
