    methods: {
      push(url) {
        appcan.navigateTo({
          url: url
        })
      },
      test: function () {
        console.log("pptest1")
      },
      kindToggle: function (e) {
        var id = e.currentTarget.id
        var list1 = this.list
        for (var i = 0, len = list1.length; i < len; ++i) {
          if (list1[i].id == id) {
            list1[i].open = !list1[i].open
          } else {
            list1[i].open = false
          }
        }
        this.list = list1
      }

    }
  }
</script>

<style>

    .ac-image {
        width: 100%;
        height: 100%;
    }
</style>



